import React, { Component } from 'react';
import { observer } from "mobx-react";
// import store from '../Store.js';
import {
	// Button,
	// ButtonGroup,
	// Jumbotron,
	Grid,
	Row,
	Col,
	FormGroup,
	// FormControl,
	// InputGroup,
} from 'react-bootstrap';
import uuid from 'uuid';
import $ from "jquery";
import { BrowserRouter, Route } from 'react-router-dom'
import { LinkContainer } from "react-router-bootstrap";

const Why = observer(class Why extends Component {
	constructor() {
		super();
		this.state = {
			production: true,
			answers: [],
			leaderboard: [],
			msg: "Enter a statement.",
			score: -1,
			submissionFlag: false,
			statement: "",
			placeholder: "Your statement goes here"
		}
	}

	componentDidMount() {
		this.getLeaderboard()
	}

	getLeaderboard() {
		let url;
		console.log(url);
		if(this.state.production) {
			url = "159.89.12.116"
		} else {
			url = "127.0.0.1:8000"
		}
		$.ajax({
			url: "http://"+url+"/leaderboard/",
			type: "GET",
			dataType: "json",
			success: function(data){
				this.setState({
					leaderboard: data.top
				}, () => {console.log(this.state.leaderboard)})
			}.bind(this),
			error: function(xhr, status, err){
				console.log(err);
			}
		});
	}

	answer(e) {
		e.preventDefault()
		if(this.refs.answer.value !== "") {
			if(!this.state.submissionFlag) {
				this.setState({statement: this.refs.answer.value, placeholder: "Your answer goes here", msg: "Why?", score: this.state.score +1, submissionFlag: true}, () => {this.refs.answer.value = ""})
				return
			}
			let answers = this.state.answers
			let answer = {
				answer: this.refs.answer.value,
				id: uuid.v4(),
			}
			answers.push(answer)
			this.refs.answer.value = ""
			this.setState({answers: answers, msg: "Why?", score: this.state.score +1, submissionFlag: true},
				() => {console.log("ANSWERS: "); console.log(this.state.answers)})
		}
	}

	submitScore() {
		let nickname = prompt("Enter your nickname please");
		let answers = this.state.answers
		console.log("ANSWERS: ")
		console.log(answers)
		// for(var i = 0; i<answers.length; i++) {
		// 	delete answers[i].id
		// }
		let data = {
			score: this.state.score,
			nickname: nickname === "" ? null : nickname,
			statement: this.state.statement,
			'answer_set': JSON.stringify(this.state.answers)
		};

		let url;
		if(this.state.production) {
			url = "159.89.12.116"
		} else {
			url = "127.0.0.1:8000"
		}
		console.log(data)
		$.ajax({
			url: "http://"+url+"/leaderboard/",
			type: "POST",
			dataType: "json",
			data: data,
			success: function(data){
				console.log("score submitted.");
			},
			error: function(xhr, status, err){
				console.log(err);
			}
		});
		this.getLeaderboard()
	}

	flagAns(ans) {
		console.log(ans.id)
		let data = {
			answer_id: ans.id
		}

		let url;
		if(this.state.production) {
			url = "159.89.12.116"
		} else {
			url = "127.0.0.1:8000"
		}
		$.ajax({
			url: "http://"+url+"/flag/",
			type: "POST",
			dataType: "json",
			data: data,
			success: function(data){
				console.log("flagged.");
			},
			error: function(xhr, status, err){
				console.log(err);
			}
		});
		this.getLeaderboard()
	}

	render() {
		let answers = this.state.answers.map((answer) => {
			return (
				<h5 key={answer.id}>{answer.answer}</h5>
			)
		})
		let leaderboard = this.state.leaderboard.map((score) => {
				let i = 0;
				let answers = score.answer_set.map((answer) => {
					i = i + 1;
					return (
						<div key={answer.id+"-"+i}>
							<p>{i}. {answer.answer} (flag: {answer.flags})</p>
							<button onClick={() => {this.flagAns(answer)}}>Flag as false/BS</button>
						</div>
					)
				})
				return (
					<tr key={score.id}>
						<td>{score.nickname}</td>
						<td>{score.score}</td>
						<td>{score.statement}</td>
						<td><LinkContainer exact to={"#"+score.id}><a>Answers</a></LinkContainer></td>
						<td><Route exact path={"#"+score.id} render={()=>{
							return (
								<div>
									{answers}
								</div>
							)
						}} /></td>
					</tr>
				)
		})
		return (
			<BrowserRouter>
				<Grid>
					<Row className="show-grid">
						<Col>
							<h1>{this.state.statement}</h1>
							{answers}
							<h4><strong>{this.state.msg}</strong></h4>
							<h4>{this.state.score}</h4>
							<form onSubmit={this.answer.bind(this)} >
								<FormGroup bsSize="large">
									<input style={{ width:1000 }} type="text" placeholder={this.state.placeholder} ref="answer" />
								</FormGroup>
							</form>
							{this.state.submissionFlag ? <button onClick={this.submitScore.bind(this)}>
								Give up and submit score to leaderboard
							</button> : null}
						</Col>
						<hr/>
						<h3>Leaderboard (Top)</h3>
						<table>
							<thead>
								<tr>
									<th>Nickname</th>
									<th>Score</th>
									<th>Statement</th>
									<th>Answers</th>
								</tr>
							</thead>
							<tbody>
								{leaderboard}
							</tbody>
						</table>
					</Row>
				</Grid>
			</BrowserRouter>
		)
	}
})

export default Why;












